                        </v-hover>
                    </v-flex>
                </v-layout>
                <v-layout row wrap>
                    <v-flex sm12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="300"
                            >
                                <v-card-title>
                                    <h3 id="Result1">效果</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                            <svg width="300" height="300">
                                                <defs>
                                                    <filter id="ftb" x="0" y="0" width="100%" height="100%">
                                                        <feTurbulence 
                                                            type="fractalNoise" 
                                                            baseFrequency="0.1"
                                                            numOctaves="5"
                                                            seed="5"
                                                            result="ftb"/>
                                                        <feDisplacementMap 
                                                            in2="ftb" 
                                                            in="SourceGraphic"
                                                            scale="50" 
                                                            xChannelSelector="B" 
                                                            yChannelSelector="B"/>
                                                    </filter>

                                                    <filter id="fdm" primitiveUnits="objectBoundingBox">
                                                        <feImage result="fi" xlink:href=""></feImage> 
                                                        <feDisplacementMap 
                                                            scale=".05" 
                                                            xChannelSelector="G" 
