<script>
import Blog from "../Blog.vue";
import { setInterval } from "timers";
export default {
    components: { Blog },
    data() {
        return {
            time: "2018-9-13",
            items: [
                {
                    title: "解释",
                    target: "#Explain"
                },
                { divider: true },
                {
                    title: "效果",
                    target: "#Result1"
                },
                { divider: true },
                {
                    title: "代码",
                    target: "#Code1"
                }
            ],
            html: `
            `,
            css: `
            `
        };
    },
    mounted() {
        this.handleWave();
    },
    methods: {
        handleWave() {
            // 生成水波纹效果，必须要用到径向渐变
            const feImage = document.querySelector("#fdm feImage");
            const ripples = document.querySelector("#ripples");
            const xlink = "http://www.w3.org/1999/xlink";

            let displacement = 0;
            let speed = 0.2;
            
            // 此函数就是用来生成svg径向渐变的datauri数据
            function generateGradient() {
                /*
                <svg version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' width="300" height="300">
                    <defs>
                        <radialGradient id="rg" r=".9"> 
                            <stop offset='0%' stop-color='#f00'></stop>
                            <stop offset='10% 'stop-color='#000'></stop>
                            <stop offset='20%' stop-color='#f00'></stop>
                            <stop offset='30%' stop-color='#000'></stop>
                            <stop offset='40%' stop-color='#f00'></stop>
                            <stop offset='50%' stop-color='#000'></stop>
                            <stop offset='60%' stop-color='#f00'></stop>
                            <stop offset='70%' stop-color='#000'></stop>
                            <stop offset='80% 'stop-color='#f00'></stop>
                            <stop offset='90%' stop-color='#f00'></stop> 
                            <stop offset='100%' stop-color='#f00'></stop>
                        </radialGradient> 
                    <defs>
                    <rect width="300" height="300" fill="url(#rg)"></rect>
                </svg>
                */
                // 特殊字符转换的规则如下
                // '&' -> '%26'
                // '|' -> '%7C'
                // '[' -> '%5B'
                // ']' -> '%5D'
                // '^' -> '%5E'
                // '`' -> '%60'
                // ';' -> '%3B'
                // '?' -> '%3F'
                // ':' -> '%3A'
                // '@' -> '%40'
                // '=' -> '%3D'
                let xlinkHref = "data:image/svg+xml;utf8,%3Csvg version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' width='300' height='300'%3E%3Cdefs%3E%3CradialGradient id='rg' r='.9'%3E";
                for (var i = 0; i < 11; i++) {
                    xlinkHref += `%3Cstop 
                                offset='${(i - 2) * 10 + displacement}%25' 
                                stop%2Dcolor='%23${i % 2 == 0 ? "3f0" : "000"}'%3E%3C/stop%3E`;
                }
                xlinkHref +=
                    "%3C/radialGradient%3E%3C/defs%3E%3Crect width='300' height='300' fill='url(%23rg)'%3E%3C/rect%3E%3C/svg%3E";
                return xlinkHref;
            }
            function AnimateOffset() {
                let xlinkHref = generateGradient();
                feImage.setAttributeNS(xlink, "href", xlinkHref);
                ripples.setAttributeNS(xlink, "href", xlinkHref);
                if (displacement <= 20) {
                    displacement += speed;
                } else {
                    displacement = 0;
                }
                window.requestAnimationFrame(AnimateOffset);
            }
            AnimateOffset();
            window.requestAnimationFrame(AnimateOffset);
        }
    },
};
</script>

